body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.findCity {
  position: relative;
  max-width: 60%;
  margin: 0px auto;
}


.findCity_form {
  display: -webkit-flex;
  display: flex;
  margin: 10px;
}

.findCity_form input{
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 5px;
  margin-right: 10px;
}
.list_container {
  max-height: 250px;
  overflow-y: auto;
  margin: 10px;
  background-color: whitesmoke;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
}
.list_container ul {
  padding: 0;
}
.list_container li {
  list-style: none;
  padding: 10px;
}

.list_container li:hover {
  background-color: bisque;
  cursor: pointer;
}
.changeBackground {
  background-color: bisque;
  cursor: pointer;
}
.bar {
  border-radius: 5px;
  overflow: hidden;
  height: 20px;
  width: 200px;
  background-color: lightslategray;
}

.innerbar {
  height: 100%;
  -webkit-transition: width 1s;
  transition: width 1s;
}
.CityDetail {
  padding: 20px;
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr 1fr;
}
.CityDetail > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.heading {
  font-family: 'Alegreya', serif;
  text-align: center;
}

